import Uppy from '@uppy/core';
import Transloadit from '@uppy/transloadit';
import { suiteTrack, isTagShop, isSocialWall } from '../../../../actions/api';

const uppyKey = '0701d3d7f8d94e92a65a9cba388b1d96';
const uppyBucket = isTagShop ? 'cloud.tagshop.ai' : isSocialWall ? 'cloud.socialwalls.com' : 'cloud.tagbox.com';
const uppyAwsKey = '47ABVORKG52UYE1KB28S';
const uppyAwsSecret = 'pcaTfeokaz28geAAns08H3yaGbOVz6ILgTptu19v';




class UppyUpload {
    constructor(beforeFileAdded, beforeUpload) {
        let uploadPath = 'media/tmpimages/';
        let bucket = uppyBucket;
        if (suiteTrack) {
            uploadPath = `ugc-suite/tmp/`;
            bucket = 'cloud.tagbox.com';
        }
        if (isTagShop) {
            uploadPath = `tmp/`;
        }
        this.uppy = new Uppy({
            meta: { type: 'avatar' },
            autoProceed: true,
            debug: true,
            allowMultipleUploads: true,
            restrictions: {
                maxFileSize: 104857600,
                maxNumberOfFiles: 1,
                minNumberOfFiles: 1,
                allowedFileTypes: ['image/*', 'video/*']
            },
            onBeforeFileAdded: beforeFileAdded,
            onBeforeUpload: beforeUpload
        })

        this.uppy.use(Transloadit, {
            params: {
                auth: {
                    key: uppyKey
                },
                steps: {
                    store: {
                        robot: '/wasabi/store',
                        bucket_region: "us-west-1",
                        result: true,
                        bucket: bucket,
                        key: uppyAwsKey,
                        secret: uppyAwsSecret,
                        path: uploadPath + '${file.id}.${file.ext}'
                    }
                }
            },
            waitForEncoding: true
            // }).use(Compressor, {
            //     quality: 0.8
        });
    }
}

export default UppyUpload;